$avatarImg: url('~assets/images/avatar.png');

.wrapper {
  position: relative;
  display: inline-block;
  border-radius: 50%;
  width: 2.5rem;
  background-color: #fff;
}

.squareImage {
  position: relative;
  opacity: 0;
  border-radius: 50%;
  z-index: 1;
  width: 100%;
  height: 100%;
  max-width: 100%;
  max-height: 100%;
}

.avatar {
  position: absolute;
  top: 0;
  left: 0;
  background-image: $avatarImg;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: contain;
  border-radius: 50%;
  width: 100%;
  height: 100%;
  z-index: 0;
}